import React, { useEffect, useState } from "react";
import { Card, Col, Row } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { DataTable, PageHeader, actionFormatter } from "../../../components";
import {
  checkValidData,
  decodeQueryData,
  firstLetterCaps,
  getFullName,
  getSortType,
  logger,
  momentUtcFormatter,
} from "../../../utils";
import { customerServices } from "../../../services";
import { dateTimeFormatWithMonth } from "../../../helpers";

function Customer() {
  const location = useLocation();
  const { search } = location;
  const [customerData, setCustomerData] = useState([]);
  const [param, setParam] = useState({});
  const [page, setPage] = useState(1);
  const [sizePerPage, setSizePerView] = useState(10);
  const [searchName, setSearchName] = useState("");
  const [defaultSort, setDefaultSort] = useState([
    {
      dataField: "",
      order: "",
    },
  ]);
  const [tableLoader, setTableLoader] = useState(false);
  const [noOfPage, setNoOfPage] = useState();
  const [totalCount, setTotalCount] = useState(0);
  //   const [searchName, setSearchName] = useState("");

  useEffect(() => {
    if (search) {
      const data = decodeQueryData(search);
      setParam(data);
      setPage(data?.page ?? 1);
      if (data?.sortType) {
        const sortData = [
          {
            order: getSortType(data?.sortType),
            dataField: data?.sortBy,
          },
        ];
        setDefaultSort(sortData);
      } else {
        setDefaultSort({
          dataField: "",
          order: "",
        });
      }
    }
  }, [location]);
  const getCustomerData = async () => {
    setTableLoader(true);
    try {
      let queryParams = {
        offset: (page - 1) * sizePerPage,
        limit: sizePerPage,
        sortBy: param?.sortBy,
        sortType: param?.sortType,
        name: searchName,
        userType: "customer",
      };
      const res = await customerServices.getCustomerList(queryParams);
      const { success, data } = res;
      if (success) {
        setCustomerData(data.rows);
        setNoOfPage(data.count > 0 ? Math.ceil(data.count / sizePerPage) : 1);
        setTotalCount(data.count);
      }
    } catch (error) {
      logger(error);
    }
    setTableLoader(false);
  };

  //   const tableReset = () => {
  //     setTableLoader(true);
  //     setHubData([]);
  //     setNoOfPage(0);
  //     setTotalCount(0);
  //   };

  //   const headerSortingClasses = (column, sortOrder) => {
  //     return sortOrder === "asc" ? "sorting_asc" : "sorting_desc";
  //   };

  //   const onSortColumn = (field, order) => {
  //     const data = { ...param };
  //     data.sortBy = field;
  //     data.sortType = order === "asc" ? "ASC" : "DESC";
  //     navigateWithParam(data, navigate, pathname);
  //     tableReset();
  //   };

  const options = () => {
    const optionsArr = [
      {
        name: "Update",
        icon: "icon ni ni-pen",
        path: "#addForm",
        action: "confirm",
        onClickHandle: () => {
          // onHandleShow(row);
        },
      },
    ];

    return optionsArr;
  };
  const columns = [
    {
      dataField: "id",
      text: "ID",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "parentCategoryId",
      text: "Hub",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "categoryOrder",
      text: "Customer Name",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) =>
        checkValidData(getFullName(row?.firstName, row?.lastName)),
    },
    {
      dataField: "phoneNumber",
      text: "Number",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) => checkValidData(row?.phoneNumber),
    },
    {
      dataField: "image",
      text: "Customer Address",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      // formatter: (cell) => imageFormatter(cell),
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "sortingOrder",
      text: "Delivery Executive",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "sortingOrder",
      text: "Customer Type",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) => checkValidData(row?.Customer?.customerType),
    },
    {
      dataField: "sortingOrder",
      text: "Wallet Amount",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "sortingOrder",
      text: "Credit Limit",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) => checkValidData(row?.Customer?.creditLimit),
    },
    {
      dataField: "sortingOrder",
      text: "Source",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "sortingOrder",
      text: "Cutomer Class",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) =>
        checkValidData(firstLetterCaps(row?.Customer?.customerClass)),
    },
    {
      dataField: "sortingOrder",
      text: "Other Language",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) => checkValidData(row?.Customer?.language),
    },
    {
      dataField: "sortingOrder",
      text: "Registered On",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) =>
        momentUtcFormatter(row?.Customer?.createdAt, dateTimeFormatWithMonth),
    },
    {
      dataField: "status",
      text: "Account Status",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(firstLetterCaps(cell)),
    },
    {
      dataField: "action",
      text: "Action",
      headerClasses: "d-flex justify-content-end",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },

      formatter: (cell, row) => actionFormatter(options(row)),
    },
  ];

  const tableReset = () => {
    setTableLoader(true);
    setCustomerData([]);
    setNoOfPage(0);
    setTotalCount(0);
  };

  const getSearchValue = (val) => {
    setSearchName(val);
    if (val) {
      tableReset();
    }
  };

  useEffect(() => {
    getCustomerData();
  }, [param]);
  return (
    <div className="nk-block-head nk-block-head-sm">
      <div className="nk-block-between">
        <Card className="w-100 rounded-2 mb-4">
          <Card.Body>
            <PageHeader heading="Customer" />
          </Card.Body>
        </Card>
      </div>
      <Card className="customerPage">
        <Card.Body className="p-4">
          <Row>
            <Col xxl={3} lg={4}>
              <div className="cardBox d-flex align-items-center">
                <div className="cardBox_left">
                  <em className="icon ni ni-user-list-fill" />
                </div>
                <div className="cardBox_right w-100">
                  <div>
                    <p className="mb-0">New Registrations</p>
                  </div>
                  <div>
                    <h2 className="mb-0">0</h2>
                  </div>
                </div>
              </div>
            </Col>
            <Col xxl={3} lg={4}>
              <div className="cardBox d-flex align-items-center">
                <div className="cardBox_left">
                  <em className="icon ni ni-users-fill" />
                </div>
                <div className="cardBox_right w-100">
                  <div>
                    <p className="mb-0">Total Customers</p>
                  </div>
                  <div>
                    <h2 className="mb-0">8336</h2>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
          <DataTable
            sizePerPagination={false}
            hasLimit
            noOfPage={noOfPage}
            sizePerPage={sizePerPage}
            page={page}
            count={totalCount}
            tableData={customerData}
            tableColumns={columns}
            // param={param}
            defaultSort={defaultSort}
            tableLoader={tableLoader}
            setSizePerPage={setSizePerView}
            tableReset={tableReset}
            getSearchValue={getSearchValue}
            searchPlaceholder="Category"
          />
        </Card.Body>
      </Card>
    </div>
  );
}

export default Customer;
